import Vue from 'vue'
import Vuex from 'vuex'
import mutations from "./mutations"
import actions from "./actions"

Vue.use(Vuex);

const store =new Vuex.Store({
    state:{
        isEnter:false,
        userInfor:{
            userBalance:65.4,
            userDiscount:10,
            userIntegral:200,
            totalPrice:0,
            carNumber:0,
        },
        restaurantMenu: [],
        carMenu:[],
    },
    mutations,
    actions,
})

export default store ;


