<template>
    <div class="account">
        <elm-header :fatherName="fatherName"></elm-header>
        <ul class="account-list">
            <li class="account-picture">
                <span>头像</span>
                <span><img src="../../../static/img/tx.jpg" alt="" /></span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/picture"></router-link>
            </li>
            <li>
                <span>用户名</span>
                <span>YZ389389</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/name"></router-link>
            </li>
            <li>
                <span>账号密码</span>
                <span>修改</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/password"></router-link>
            </li>
            <li>
                <span>手机号</span>
                <span>150****5397</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/tel"></router-link>
            </li>
            <li>
                <span>微信账号</span>
                <span>解绑</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/weixin"></router-link>
            </li>
            <li>
                <span>注销账号</span>
                <span>注销后无法恢复，请谨慎操作</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/cancel"></router-link>
            </li>
            </ul>
            <p class="account-signOut">退出当前账号</p>
    </div>
</template>
<script>
export default{
    name:"account",
    data () {
        return {
            fatherName:"我的账号",

        }
    }
}

</script>

<style scoped>
.account-list{
    margin-top:0.2rem;
    background:#fff;
    padding:0 0.3rem;
}
.account-list li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom:1px solid #f3f1f1;
    padding:0.3rem 0;
}
.account-list li span{
    color:#999;
    font-size:0.26rem;
}
.account-list li span:first-child{
    flex:1;
    width:80%;
    color:#555;
}

.account-list li a{
    color:#999;
    font-size:0.36rem;
    margin-left:0.2rem;

}
.account-picture img{
    width:0.8rem;
    height:0.8rem;
    border-radius: 100%;
}
.account-signOut{
    position: fixed;
    bottom:0;
    left:0;
    width:100%;
    padding:0.3rem 0;
    text-align: center;
    background:#fff ;
    font-size:0.36rem;
    color:rgb(240, 72, 72);
}


</style>


