<template>
      <div>
        <template v-for="(items,index) in list" >
            <template v-for="(option,i) in items.item">
                <p>{{option.title}}</p>
                <span v-for="(o,i) in option.menu" >
                    {{o.title}}
                </span>
            </template>
            <hr>
        </template>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        data() {
            return {
               list:[
                   {
                    name:"蔬菜",
                    item:[{title:"西红柿"},{title:"辣椒",menu:[{title:"红辣椒"},{title:"青辣椒"},{title:"变态辣椒"}]},{title:"红薯"},{title:"菠菜"}]
                   },
                   {
                    name:"海鲜",
                    item:[{title:"鱼"},{title:"虾"},{title:"贝壳累"},{title:"海参"}]
                   },
                   {
                    name:"肉类",
                    item:[{title:"牛肉"},{title:"鸡肉",menu:[{title:"野鸡"},{title:"家养"},{title:"圈养"}]},{title:"羊肉"},{title:"猪肉"}]
                   }
               ]
            }
        },
        methods: {

        }
    }
</script>

<style scoped>
</style>


