<template>
    <div class="remarks">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="remarks-main">
            <textarea name="" id="" v-model="remarksTips" @focus="remarksTipsInit"></textarea>
            <span>0/50</span>
            <div>
                <h1>
                    <span class="remarks-list-titel">快捷标签</span>
                    <span>编辑</span>
                </h1>
                <p class="remarks-list">
                    <span>不吃辣</span>
                    <span>少放辣</span>
                    <span>多放辣</span>
                    <span>不吃蒜</span>
                    <span>少醋</span>
                    <span>多醋</span>
                    <span>不吃香菜</span>
                    <span>今天我生日，可不可以要惊喜！</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    name:"remarks",
    data () {
        return {
            fatherName:"添加备注",
            remarksTips:"请输入口味、偏好等要求，不支持额外代购商品哦~",
        }
    },
    methods: {
        remarksTipsInit:function(){
            this.remarksTips='';
        }
    }
}

</script>

<style scoped>
.remarks{
    background: #fff;
    overflow: hidden;
}
.remarks-main{
    height:13rem;
    position: relative;
    padding:0.3rem 0.2rem;
}
.remarks-main>textarea{
    width:100%;
    height:3rem;
    padding:0.2rem;
    box-sizing: border-box;
}
.remarks-main>span{
    position: absolute;
    right:0.3rem;
    top:2.9rem;
    font-weight: bold;
} 
.remarks-main h1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0.4rem 0;
}
.remarks-main h1 span{
    font-size:0.3rem;
}
.remarks-list-titel{
    color:#999;
}
.remarks-list{
    font-size:0;
}
.remarks-list span{
    display: inline-block;
    padding:0.1rem;
    border:1px solid #e6e6e6;
    margin:0 0.3rem 0.3rem 0;
    font-size:0.2rem;
}
</style>


