<template>
    <div class="address">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="address-items">
            <mt-field label="联系人"  v-model="addAddress.name"></mt-field>
            <mt-field label="联系电话" type="tel" v-model="addAddress.tel"></mt-field>
            <mt-field label="送餐地址" v-model="addAddress.content"></mt-field>
            <mt-field label="标签"  v-model="addAddress.marker"></mt-field>
        </div>
        <mt-button class="address-button" type="primary" size="large" @click="addItem">确定</mt-button>
    </div>
</template>

<script>
export default{
    name:"addAddress",
    data () {
        return {
            fatherName:"添加地址",
            msg:"ces",
            addAddress:{
                marker:"家/公司/学校",
                content:"详细地址（如门牌号等）",
                tel:"您的电话",
                name:"您的姓名",
                sex:"女士",
            },
        }
    },
    methods: {
        addItem:function(){
            this.$router.push("/payment");    
        }
    },
}

</script>

<style scoped>
.address-items{
    background: #fff;
}
.mint-field-core{
    color:#fff;
}
.address-button{
    width:96%;
    margin:0.3rem auto 0;
}


</style>


